// header styles begun
.header {
  &__top {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 1.5em 0; }
  &__logo {
    display: inline-block;
    margin: 0 3em;
    max-width: 15em;
    width: 100%;
    line-height: 0; }
  .icon-kbp {
    fill: #fff; }
  .icon-plane {
    fill: #673774;
    width: 10em; }
  &__bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1em 2em;
    background-color: #402148; }
  &__left {
    display: flex;
    align-items: center; }
  &__icon {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 10em;
    height: 5em;
    background: #FFCF18;
    padding: .3em .5em;
    border-radius: .5em; }
  &__title {
    font-size: 5em;
    font-style: italic;
    color: #fff;
    padding-left: 1em;
    letter-spacing: 4px; }
  &__date {
    display: inline-block;
    min-width: 5em;
    text-align: center;
    margin-right: 1em;
    color: #fff;
    font-size: 4em;
    font-weight: 700;
    letter-spacing: 3px; }
  &__time {
    display: inline-block;
    min-width: 4em;
    text-align: center;
    color: #FFCF18;
    font-size: 5em;
    font-weight: 700;
    letter-spacing: 5px; } }
// header styles end

// content styles begun
.timetable {
  &__head {
    background-color: #320e3b;
    border-top: 2px solid #cccccc;
    border-bottom: 2px solid #cccccc; }
  &__title {
    padding: 1em .3em;
    font-size: 3em;
    color: #efefef;
    text-align: left;
    &--center {
      text-align: center; } }
  &__row {
    padding: 0 1.5em;
    background-color: #6c4079;
    &--hide {
      display: none; }
    &:nth-child(2n) {
      background-color: #4d2957; } }
  &__cell {
    padding: .5em;
    font-size: 2.2em;
    color: #fff;
    font-weight: 400;
    &--yellow {
      font-weight: 700;
      color: #FFCF18; }
    &--center {
      text-align: center; }
    &--bold {
      font-weight: 700; } } }

