html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

body {
  background-color: #673774;
  font-family: 'Roboto', sans-serif;
}

.header__top {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 1.5em 0;
}

.header__logo {
  display: inline-block;
  margin: 0 3em;
  max-width: 15em;
  width: 100%;
  line-height: 0;
}

.header .icon-kbp {
  fill: #fff;
}

.header .icon-plane {
  fill: #673774;
  width: 10em;
}

.header__bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1em 2em;
  background-color: #402148;
}

.header__left {
  display: flex;
  align-items: center;
}

.header__icon {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 10em;
  height: 5em;
  background: #FFCF18;
  padding: .3em .5em;
  border-radius: .5em;
}

.header__title {
  font-size: 5em;
  font-style: italic;
  color: #fff;
  padding-left: 1em;
  letter-spacing: 4px;
}

.header__date {
  display: inline-block;
  min-width: 5em;
  text-align: center;
  margin-right: 1em;
  color: #fff;
  font-size: 4em;
  font-weight: 700;
  letter-spacing: 3px;
}

.header__time {
  display: inline-block;
  min-width: 4em;
  text-align: center;
  color: #FFCF18;
  font-size: 5em;
  font-weight: 700;
  letter-spacing: 5px;
}

.timetable__head {
  background-color: #320e3b;
  border-top: 2px solid #cccccc;
  border-bottom: 2px solid #cccccc;
}

.timetable__title {
  padding: 1em .3em;
  font-size: 3em;
  color: #efefef;
  text-align: left;
}

.timetable__title--center {
  text-align: center;
}

.timetable__row {
  padding: 0 1.5em;
  background-color: #6c4079;
}

.timetable__row--hide {
  display: none;
}

.timetable__row:nth-child(2n) {
  background-color: #4d2957;
}

.timetable__cell {
  padding: .5em;
  font-size: 2.2em;
  color: #fff;
  font-weight: 400;
}

.timetable__cell--yellow {
  font-weight: 700;
  color: #FFCF18;
}

.timetable__cell--center {
  text-align: center;
}

.timetable__cell--bold {
  font-weight: 700;
}

@font-face {
  font-family: 'Roboto';
  font-style: normal;
  src: url("../fonts/Roboto.eot"), url("../fonts/Roboto.eot") format("embedded-opentype"), url("../fonts/Roboto.woff2") format("woff2"), url("../fonts/Roboto.woff") format("woff"), url("../fonts/Roboto.ttf") format("truetype"), url("../fonts/Roboto.eot?#iefix") format("embedded-opentype"), url("../fonts/Roboto.svg") format("svg");
}
